
@media only screen and (max-width: 600px) {
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
    body {
        background-color: lightblue;
    }

    .inbox {
        color: grey;


    }

    .menu_item {
        display: flex;

        justify-content: space-between;
        gap: 2rem;
        width: auto;
        padding: 3rem;
        
        border-bottom: 2.5px solid #1c9ab6;
     
        list-style: none;
        
    }

    .gear {
        color: grey;
    }

    .box {
        font-size: 30pt bold;
        align-items: center;
        color: #6e8ca0;
    }

    .menu__top--searchBar {
        position: relative;
        border-bottom: 2.5px solid #1c9ab6;
     
    }

    .menu__top--searchBar input {
        width: 100%;
        padding: 1.5em;
        border-radius: 2px;
        border: none;
        background-color: #c8d1d3;
    }

    .menu__top--searchBar button {
        padding: 2em;
        background-color: transparent;
        border: none;
        color: #f0f3f4;
        position: absolute;
        right: 0;
    }
img{
    width: 100px;
  height: 100px;
  border-radius: 50%;
}
.category__head{
   
  font-size: 18px;
  text-transform: uppercase;
  
  
}
.category__logo{
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: #f0f3f4;
}
.category__summary{
    display: grid;
    grid-template-columns: 16fr 52fr 11fr;
    padding-bottom: 5%;
    padding-top: 5%;
}
.category__container{
    border-bottom: 2.5px solid #1c9ab6;
    display: grid;
    grid-template-columns: 1fr 3fr;
    grid-template-rows: auto auto;
    border-top: 2.5px solid #1c9ab6;
    padding-bottom: 5%;
    padding-top: 5%;

    img{
        grid-area: 1/1/3/2;
        align-self: center;
    }
    h3{
        align-self: self-end;
    }
}

}